body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
}

.app{
  background-color: black;
}


.header{
    display:flex;
    padding:15px 20px;
    justify-content: space-between;
    position:-webkit-sticky;
    position:sticky;
    /* background-color:transparent; */
    background-color:rgba(0,0,0,0.9);
    /* background-color:white; */
    top:0;
    z-index:100;
    /* border-bottom: 1px solid #374785 */
}
.header__left{
    display:flex;
    text-align:center; 
    /* border-bottom: 5px solid #A8D0E6 ; */
}

.header__left > h1{
    
    color:white;
    /* color: #374785; */
    font-family: 'Poppins', sans-serif;
    font-size:40px;
}

.header__left >h1 >span{
    color: #FC4445
    /* color: #F76C6C; */
}

.header__right{
    display:flex;
}

.header__button{
    padding:10px;
    margin-left:5px;
    margin-right:5px;
}
.header__button:hover{
    padding:10px;
    background-color:#FC4445;
    border-radius: 10px;
    
}



#button{
    font-family: 'Poppins', sans-serif;
    background-color: transparent;
    color: white;
    padding:1px;
    /* color: #374785; */
    font-size:20px;
    border: none;
    outline-width: 0;
}

#button > .active{
    border-bottom: 5px solid #FC4445;
    font-weight:bold;
    font-size:22px;
}
.header__toggle{
    position:absolute;
    top: .75rem;
    right: 10%;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height:21px;
}

.header__bar{
    height: 3px;
    width:100%;
    background-color: white;
    border-radius: 10px;
}

@media (max-width: 900px){
    .header{
        flex-direction: column;
    }
    .header__right{
       display: none  
    }

    .header__left > h1{
        font-size: 20px;
    }

    .header__toggle{
        display: flex;
    }

    .active{
        display: flex;
        flex-direction: column;  
        justify-content: center;
        align-items: center;
        transition: ease-in 1s;
    }
    
}





.work{
    min-height:100vh;
    position:relative;
    display:flex;
    align-items: center;
    justify-content: center;
    background-color: white;
}

.work__container{
    min-height:100vh;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content: center;
}
.work__projectContainer{
    width:100vw;
    display:flex;
    justify-content: flex-start;
}

#work__projectContainerCenter{
    justify-content: flex-end;
}
.work__project{
    display:flex;
    margin:40px;
    margin-bottom:60px;
    /* box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9); */
    border-radius: 20px;
}
.work__project > img{
 margin:60px;
 width:480px;
 height:320px;
 border-radius: 10px;
 box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9);
}

.work__project > video{
    margin:60px;
    width:480px;
    height:320px;
    border-radius: 10px;
    box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9);
}

.work__projectInfo{
    display:flex;
    flex-direction:column;
    justify-content: baseline;
    margin:20px;
}

.work__projectText{
    display:flex;
    flex-direction: column;
}

.work__projectText > h1{
    font-family: 'Poppins', sans-serif;
    font-size:45px;
    margin-bottom: 30px;
}

.work__projectText > p{
    font-size:25px ;
    font-family: 'Playfair Display', serif;
    width:700px;
}

.work__projectBottom{
    display:flex;
    align-items: center;
    justify-content: flex-start;
    margin-top:20px;
}
.work__projectBottom > li{
    list-style-type: none;

}
.work__projectBottom > li > ul{
    font-size:25px ;
    font-family: 'Poppins', sans-serif;
    flex:1 1;
    
}
.work__projectButtons{
    display:flex;
    justify-content: space-evenly;
}

.work__projectButtons .MuiIconButton-root{
    background-color: white;
    /* box-shadow: 0px 10px 53px 0px rgba(0, 0, 0, 0.3) !important; */
    margin:10px;
    padding: 1.5vw
}

.work__playButton{
    color: #FC4445;
}

.work__codeButton{
    color:Black
}

.work__demoButton{
    color:#4056a1
}

@media (max-width:700px){
    .work__projectContainer{
        flex-direction: column;
    }
    .work__project{
        flex-direction: column-reverse;
    }
    .work__projectInfo{
        align-items:center;
        margin:10px;
    }

    #work__projectCenter{
        flex-direction: column;
    }

    .work__project > img{
        margin:15px;
        width:300px;
        height: 210px;
        align-self: center;
        /* margin-left: auto;
        margin-right: auto; */
    }

    .work__project > video{
        margin:15px;
        width:300px;
        height: 210px;
        align-self: center;
        /* margin-left: auto;
        margin-right: auto; */
    }

    .work__projectText > h1{
        font-size:30px;
    }

    .work__projectText > p{
        width:260px;
        font-size: 14px;
    }
    .work__projectBottom{
        justify-content: flex-end;
    }

    .work__projectBottom > li > ul{
        font-size: 12px;
        margin-right:15px;
        margin-left:0px;
    }

    .work__projectButtons .MuiIconButton-root{
        padding: 5px;
        margin:5px;
    }
}

@media (min-width:701px) and (max-width:1400px){
    .work__projectContainer{
        flex-direction: column;
    }
    .work__project{
        flex-direction: column-reverse;
    }
    .work__projectInfo{
        align-items:center;
        margin:10px;
    }

    #work__projectCenter{
        flex-direction: column;
    }
}

.home{
    height:100vh;
    /* background-color:#19181A; */
    background-color:rgba(0,0,0,0.9);
    background-position: center center;
    background-size:contain;
    background-repeat:no-repeat;
    background-image: url(/static/media/home_paulin.e52bd0ba.jpg)
}

.home__box{
    height:100%;
    width:100%;
    background-color: rgba(0,0,0,0.6) ;
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    
}
.home__boxName{
    width:17ch;
    color:white;
    font-family: 'Poppins', sans-serif;
    animation: typeName 7s steps(11) 3s infinite ;
    overflow:hidden;
    white-space: nowrap;
    border-right: 5px solid white;
    font-size:100px
}
.home__boxName > span{
    color: #FC4445;
    font-weight:bold;
}

.home__boxRole{
    margin-top:10px;
    width:17ch;
    color:white;
    font-family: 'Poppins', sans-serif;
    animation: typeRole 7s steps(21) 3s infinite ;
    overflow:hidden;
    white-space: nowrap;
    border:none;
    font-size:50px;
}

@keyFrames typeName{
    0%{
        width:0ch;
    }
    45%{
        width:11ch;
        
        
    }
    99%{
        border-bottom:10px solid white;
    }
    100%{
        width:11ch;
        border: none;
        border-bottom:10px solid white;
    }
}

@keyframes typeRole{
    0%{
        width:0ch;
    }
    55%{
        width:0ch;
        
    }
    70%{
        width:15ch;
        border-right: 5px solid white;
    }
    100%{
        width:15ch;
        border-right: 5px solid white;
    }
}

@media (max-width:750px){
    .home__boxName{
        font-size:30px;
    }

    .home__boxRole{
        font-size: 25px;
    }
}



.about{
    min-height:100vh;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    width:100vw;
    /* background-color:black; */
    background-color:#FC4445;
    display:flex;
    justify-content:center;
    /* min-height: fit-content; */
}

.about__container{
    display:flex;
    flex-direction:column;
    text-align: center;
    justify-content: center;
}

.about__container > h1{
    color:white;
    /* font-family: 'Poppins', sans-serif; */
    font-family: 'Oswald', sans-serif;
    font-size:60px;
    margin-bottom: 50px;
    margin-top: 50px;
    margin-left:auto;
    margin-right:auto; 
}
.about__container > h1 >span{
    /* color:#FC4445; */
    color:white;
}

.about__content{
    display:flex;
    align-items:center;
    justify-content: space-between;
    margin:10px;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
}

.about__content > img{
    margin:20px;
    margin-left: 20px;
    object-fit: contain;
    height:300px;
    border-radius: 10px;
    box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9);
    transition: transform 100ms ease-in;
}

.about__context > img:hover{
    transform: scale(1.07)
}

.about__text{
    width:700px;
    text-align:justify;
    margin-right:10px;
}

.about__text> p{
    font-size:20px ;
    /* font-family: 'Playfair Display', serif; */
    color:white;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}



.about__hiddenContainer{
    margin-right:60px;
    width:360px;
    height:480px;
}

@media (max-width: 700px){
    .about{
        min-height:100vh;
        height:-webkit-fit-content;
        height:-moz-fit-content;
        height:fit-content;
    }
    .about__container > h1 {
        font-size: 18px;
        margin-top: 20px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
    }
    .about__content{
        flex-direction: column;
    }

    .about__text{
        width: 280px;
    }

    .about__text > p {
        font-size: 13px;
    }

    .about__hiddenContainer{
        display:none
    }

    .about__content > img{
        margin-top:10px;
        height:200px;
        align-self: center;
    }
}

@media (min-width:701px) and (max-width:1400px){
    .about__container > h1{
        font-size:40px;
    }
    .about__content{
        flex-direction: column;
    }

    .about__text{
        width: 500px;
    }

    .about__text > p {
        font-size: 18px;
    }
}




.skills{
    /* background-color:white; */
    background-color: #FC4445;
    display:flex;
    flex-direction: column;
    align-items:flex-start;
    min-height:-webkit-fit-content;
    min-height:-moz-fit-content;
    min-height:fit-content;
}


.skills__row{
    margin-top:60px;
    margin-left:100px;
    margin-right:100px;
    display:flex;
    flex-direction: column;
}

.skills__row > h1{
    color:white;
    margin:15px;
    font-size:30px;
    font-family: 'Oswald', sans-serif;
}

.skills__logos{
    display:flex;
    align-items:center;
}

.skills__leftColumn{
    display:flex;
    
}

.skills__rightColumn{
    display:flex;
}

.skills__logo{
   height:100px;
   min-width:100px;
   width:-webkit-fit-content;
   width:-moz-fit-content;
   width:fit-content;
   padding-left:10px;
   padding-right: 10px;
   margin:20px;
   box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9);
   display:flex;
   align-items:center;
   justify-content: center;
   border-radius:20px;
   background-color:white;
   transition: transform 100ms ease-in;
}

.skills__logo:hover{
    transform: scale(1.07)
}

.skills__logo > h1{
    /* color:white; */
    color:#FC4445;
    font-family: 'Oswald', sans-serif;
}

.skills__button{
    align-self: center;
    margin:40px;
    height:100px;
    display:flex;
    align-items:center;
    justify-content: center;
}

.skills__button >a > button{
    padding:25px;
    box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9);
    border-radius:20px;
    color:#FC4445;
    font-family: 'Oswald', sans-serif;
    font-size:25px;
    cursor: pointer;
    outline-width:0;
    transition: transform 100ms ease-in;
}

.skills__button > a > button:hover{
    background-color:lightgray;
    transform: scale(1.07)
}

@media (max-width: 700px){
    .skills__logos{
        margin-left: auto;
        margin-right:auto;
        align-items:flex-start
    }

    .skills__leftColumn{
        flex-direction: column;
    }


    .skills__rightColumn{
        justify-self:flex-start;
        flex-direction: column;
    }
    .skills__row{
        width:100%;
        margin-top:20px;
        margin-left:10px;
        margin-right:10px;
    }
    .skills__row > h1{
        font-size: 18px;
    }

    .skills__logo{
        height:50px;
        min-width:70px;
        width:-webkit-fit-content;
        width:-moz-fit-content;
        width:fit-content;
        margin:10px;
        border-radius:5px
    }

    .skills__logo > h1{
        font-size:18px;
    }

    .skills__button >a > button{
        border-radius: 10px;
        padding:15px;
        font-size: 18px;
    }
}

@media (min-width:701px) and (max-width:1400px){
    .skills__logos{
        margin-left: auto;
        margin-right:auto;
        align-items:flex-start;
    }
    .skills__leftColumn{
        flex-direction: column;
    }
    .skills__rightColumn{
        justify-self:flex-start;
        flex-direction: column;
    }

    .skills__row{
        width:100%;
        margin-top:20px;
        margin-left:10px;
        margin-right:10px;
    }
}
.contact{
    height:100vh; 
    background-color:#19181A;
    background-position: center center;
    background-size:cover;
    background-repeat:no-repeat;
    background-image: url("https://zumpermedia.s3.amazonaws.com/blog/wp-content/uploads/2019/02/20155959/computer-at-coffee-shop.jpg")
}

.contact__box{
    height:100%;
    width:100%;
    background-color: rgba(0,0,0,0.6) ;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size:20px
}

.contact__box > h1{
    width:8ch;
    color:white;
    font-family: 'Poppins', sans-serif;
    animation: typeContact 7s steps(8) 3s infinite ;
    overflow:hidden;
    white-space: nowrap;
    border-right: 5px solid white;
    margin-top: 20px
}


@keyframes typeContact{
    0%{
        width:0ch;
    }
    20%{
        width:8ch;
    }
    100%{
        width:8ch;
    }
}

.contact__form{
    display:flex;
    flex-direction: column;
    margin-top:10px;
}

.contact__form > label{
    color:white;
    font-family: 'Poppins', sans-serif;
    font-size:25px;
}

.contact__form>input{
    padding:10px;
    border-radius:15px;
    box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9);
    width:700px;
    margin-bottom:20px;
    font-size:20px;
    font-family: 'Poppins', sans-serif;
    outline-width: 0;
    margin-top:5px;
}

.contact__form>textarea{
    padding:20px;
    border-radius:15px;
    box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9);
    width:700px;
    margin-bottom:15px;
    font-size:20px;
    font-family: 'Poppins', sans-serif;
    outline-width: 0;
    margin-top:5px;
    height:150px;
    resize:none;
}

.contact__form > button{
    margin-top:10px;
    align-self: center;
    padding: 10px;
    width:150px;
    font-size: 18px;
    border-radius:15px;
    /* background-color:white; */
    background-color:#FC4445;
    color:white;
    outline-width: 0;
    border:none;
    box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9);
    font-family: 'Poppins', sans-serif;
    font-weight:bold;
    transition: transform 100ms ease-in;
    cursor: pointer;
}

.contact__form > button:hover{
    transform: scale(1.13)
}

.contact__bottom{
    display:flex;
    align-items:center;
    width:100%;
    justify-content: space-between;

}

.contact__invisible{
    width: 33%;
    height: 5px;
}

.contact__bottom>p{
    color:white;
    font-family: 'Poppins', sans-serif;
    font-size:25px;
    font-weight:bold;
    flex:1 1;
}

.contact__icons{
    display:Flex;
}

.contact__icon{
    margin-right:20px;
    background-color:white;
    padding:10px;
    border-radius:50%;
    box-shadow:0px 5px 17px -7px rgba(0, 0, 0, 0.9);
    transition: transform 100ms ease-in;
}

.contact__icon:hover{
    transform: scale(1.15)
}

#linkedIn{
    color:#0e76a8;
}

#gitHub{
    color:black;
}

#faceBook{
    color:#3b5998;
}

@media (max-width:800px){
    .contact{
        min-height: 100vh;
        height:-webkit-fit-content;
        height:-moz-fit-content;
        height:fit-content;
    }
    .contact__box{
        min-height:100vh;
        height:-webkit-fit-content;
        height:-moz-fit-content;
        height:fit-content;
    }

    .contact__form>input{
        width:260px;
        height:25px;
        font-size: 14px;
        margin-left:5px;
    }

    .contact__form>textarea{
        width:250px;
        height:100px;
        margin-left:5px;
    }

    .contact__bottom{
        flex-direction: column;
    }

    .contact__bottom>p{
        margin-top:10px;
        align-self: center;
        flex: 0 1;
        font-size: 14px;
    }
    .contact__form > label{
        font-size: 16px;
    }
    .contact__icon{
        margin:5px;
        padding:5px;
    }
}
